/*------------------------------------*\
    $mixin
\*------------------------------------*/
@mixin container-lg{
  max-width: 1920px;
  margin: 0 auto;
}

@mixin container-md{
  max-width: 1320px;
  padding: 0 12px;
  margin: 0 auto;
}

@mixin container-sm{
  max-width: 696px;
  margin: 0 auto;
}

@mixin container-xs{
  max-width: 348px;
  margin: 0 auto;
}


/*------------------------------------*\
    $RWD
\*------------------------------------*/


@mixin pad{
  @media (max-width:992px){
    @content;
  }
}

@mixin mobile{
  @media (max-width:767px){
    @content;
  }
}