.optical{
  & .subBanner{
    &__container{
      & > .banner{
        &__left, &__right{
          width: 50%;
          height: 296px;
          @include mobile{
            height: 136px;
          }
          background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/product-header-1.png?raw=true);
          @include pad{
            background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/product-header-sm-1.png?raw=true);
          }
          background-size: cover;
          background-position: center center;
        }
        &__right{
          background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/product-header-2.png?raw=true);
          @include pad{
            background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/product-header-sm-2.png?raw=true);
          }
        }
      }
    }
  }
}

.optical{
  & .product{
    padding-top: $spacer * 5;
    @include pad{
      padding-bottom: $spacer * 2.5;
    }
    @include mobile{
      padding-top: $spacer * 2.5;
      padding-bottom: $spacer * 2.5;
    }
    &__title{
      font-family: $font-family-English;
      color: $color-font-caption;
    }
    &__subTitle{
      color: $color-font-caption;
    }
    @include pad{
      &__title{
        font-size: $font-size-base * 2.625;
      }
      &__subTitle{
        font-size: $font-size-base * 2;
      }
    }
    @include mobile{
      &__title{
        font-size: $font-size-base * 1.5;
      }
      &__subTitle{
        font-size: $font-size-base * 1.5;
      }
    }
    &__list{
      &__item{
        width: 25%;
        @include pad{
          width: 50%;
        }
        @include mobile{
          width: 100%;
        }
        display: flex;
        flex-wrap: wrap;
        & .item{
          &__body{
            width: 100%;
            background-color: #ffffff;
          }
          &__name, &__price{
            width: 50%;
            font-size: $spacer * 1.5;
          }
          &__name{
            color: $color-font-caption;
          }
        }
      }
      &__item{
        margin-bottom: $spacer * 5;
        @include pad{
          margin-bottom: $spacer * 2.5;
        }
        @include mobile{
          margin-bottom: $spacer * 1.5;
        }
        .item__color{
          display: inline-block;
          width: 24px;
          height: 24px;
          border-radius: 4px;
          & + .item__color{
            margin-left: $spacer / 2 ;
          }
          &--primary{
            background-color: $color-bgc-Select-primary;
          }
          &--secondary{
            background-color: $color-bgc-Select-secondary;
          }
        }
      }
      @include mobile{
        &__item{
          position: relative;
          padding-top: 65.5173%;
          overflow: hidden;
          & img{
            position: absolute;
            top: -12px;
            
          }
          .item__body{
            position: absolute;
            bottom: 0px;
          }
        }
      }
      &__item:nth-of-type(1), :nth-of-type(5), :nth-of-type(9){
        padding-right: 18px;
      }
      &__item:nth-of-type(2), :nth-of-type(6), :nth-of-type(10){
        padding-right: 12px;
        padding-left: 6px;
      }
      &__item:nth-of-type(3), :nth-of-type(7), :nth-of-type(11){
        padding-right: 6px;
        padding-left: 12px;
      }
      &__item:nth-of-type(4), :nth-of-type(8), :nth-of-type(12){
        padding-left: 18px;
      }
      @include pad{
        &__item:nth-of-type(odd){
          padding-left: 0px;
          padding-right: 12px;
        }
        &__item:nth-of-type(even){
          padding-left: 12px;
          padding-right: 0px;
        }
      }
      @include mobile{
        &__item:nth-of-type(odd){
          padding-right: 0px;
        }
        &__item:nth-of-type(even){
          padding-left: 0px;
        }
      }
    }
  }
}

.pagination{
  margin-bottom: $spacer * 5;
  @include mobile{
    margin-bottom: $spacer * 4;
  }
  &__container{}
  &__list{
    width: 28.7038%;
    @include pad{
      width: 53.4483% ;
    }
    @include mobile{
      width: 100% ;
    }
    &__item{
      border-top: 1px solid $color-border-pagination;
      border-bottom: 1px solid $color-border-pagination;
      & a{
          display: inline-block;
          width: 100%;
        }
    }
    &__item + &__item{
      border-left: 1px solid $color-border-pagination;
    }
    &__item:nth-of-type(1){
        width: 22.23%;
        border-left: 1px solid $color-border-pagination;
    }
    &__item:nth-of-type(7){
        width: 22.23%;
        border-right: 1px solid $color-border-pagination;
    }
    & :nth-child(1n + 2):not(:last-child){
        width: 11.12%;
    }
    & :nth-last-child(1) a{
      background-color: #F2F2F2;
    }
  }
  &__list{
    &__item{
      background-color: #fff;
      & a{
        line-height: 48px;
        color: $color-font-pagination;
        &:hover{
          color: $color-font-hover;
          background-color: $color-hover-Select-pagination;
        }
      }
    }
  }
}