  .series{
    &__container{
      max-width: 1320px;
      padding: 0 12px;
      margin: 0 auto;
      @include pad{
        max-width: 768px;
        padding: 0;
      }
      @include mobile{
        max-width: 375px;
        padding: 0;
      }
    }
    &__list{
      background-color: $color-bgc-link;
      &__item{
        position: relative;
        width: 33.179%;
        @include mobile{
          width: 32.8%;
        }
        background-color: #ffffff;
        &:hover{
          .item__active{
            display: block;
          }
        }
      }
      & .item__link{
        display: block;
        width: 100%;
        padding: $spacer * 1.5 0;
        @include pad{
          font-size: $spacer * 1.5;
          padding: $spacer * 1 0;
        }
        @include mobile{
          font-size: $spacer * 1;
        }
        line-height: 1.5;
        color: $color-font-caption;
      }
      & .item__active{
        display: none;
        position: absolute;
        background-color: $color-bgc-primary;
        top: 88px;
        @include pad{
          top: 60px;
        }
        @include mobile{
          top: 48px;
        }
        left: -1px;
        right: -1px;
        bottom: 0px;
      }
    }
  }