/*------------------------------------*\
    $question
\*------------------------------------*/
.question{
  padding-top: $spacer * 6;
  padding-bottom: $spacer * 5;
  &__container{
    @include container-md;
    padding-right: $spacer * 27.5;
  }
  &__title{
    font-size: $font-size-base * 3;
    color: $color-font-caption;
    &::before{
      content: "";
      width: 2px;
      height: 2px;
      padding-right: $spacer * 0.75;
      margin-left: -4px;
      border-left: 8px solid $color-border-title;
    }
  }
  &__subtitle{
    color: $color-font-caption;
  }
  &__list{
    &__item{
      .item{
        &__title{
          font-size: $font-size-base * 1.25;
          color: $color-font-caption;
        }
        &__caption{
          font-size: $font-size-base * 1;
          color: $color-font-caption;
        }
      }
    }
  }
  @include pad{
    padding: $spacer * 5 0;
    &__container{
      @include container-sm;
      padding-right: 0px;
    }
  }
  @include mobile{
    padding: $spacer * 2.5 0;
    &__container{
      @include container-xs;
    }
    &__title{
      font-size: $font-size-base * 1.5;
      margin-bottom: $spacer * 1.5 !important;
    }
    &__list{
      &__item{
        margin-bottom: $spacer * 1.5 !important;
        &:nth-of-type(4){
          & h3{
            margin-right: -$spacer / 2;
          }
        }
      }
    }
  }
}