/*------------------------------------*\
    $utils
\*------------------------------------*/
.container-lg{
  width: 1920px;
}
.container-md{
  width: 1296px;
}
.container-sm{
  width: 304px;
}

.d-inline{
  display: inline;
}
.d-inline-block{
  display: inline-block;
}
.d-block{
  display: block;
}

.w-100{
  max-width: 100%;
}
.w-50{
  max-width: 50%;
}

.mx-auto{
  margin: 0 auto;
}
.my-auto{
  margin: auto 0;
}

.text-start{
  text-align: left;
}
.text-center{
  text-align: center;
}
.text-end{
  text-align: right;
}

.decoration-none{
  text-decoration: none;
}

.bg-primary{
  background-color: $color-bgc-primary;
}

.text-white{
  color: $color-font-link;
}

// font-size
@each $key,$value in $font-size{
  .fs-#{$key}{
    font-size: $value;
  }
}

.fst-italic{
  font-style: italic;
}

.fw-light{
  font-weight: 300;
}
.fw-regular{
  font-weight: 400;
}
.fw-medium{
  font-weight: 500;
}
.fw-semiBold{
  font-weight: 600;
}
.fw-bold{
  font-weight: 700;
}
.fw-black{
  font-weight: 900;
}


// flex
.flex-wrap{
  flex-wrap: wrap;
}
.d-flex{
  display: flex;
}
.flex-row{
  flex-direction: row;
}
.flex-column{
  flex-direction: column;
}
.justify-start{
  justify-content: flex-start;
}
.justify-center{
  justify-content: center;
}
.justify-end{
  justify-content: flex-end;
}
.justify-between{
  justify-content: space-between;
}
.justify-evenly{
  justify-content: space-evenly;
}
.justify-around{
  justify-content: space-around;
}
.flex-row-reverse{
  flex-direction: row-reverse;
}
.flex-column{
  flex-direction: column;
}

.align-start{
  align-items: flex-start;
}
.align-center{
  align-items: center;
}
.align-end{
  align-items: flex-end;
}


// container
.container{
  max-width: 1320px;
  padding: 0 $spacer * 0.75;
  margin: 0 auto;
  @include pad{
    max-width: 720px;
  }
  @include mobile{
    max-width: 372px;
  }
}


// padding-top
@each $key,$value in $spacers{
  .pt-#{$key}{
    padding-top: $value;
  }
}

// padding-bottom
@each $key,$value in $spacers{
  .pb-#{$key}{
    padding-bottom: $value;
  }
}

// margin-top
@each $key,$value in $spacers{
  .mt-#{$key}{
    margin-top: $value;
  }
}

// margin-bottom
@each $key,$value in $spacers{
  .mb-#{$key}{
    margin-bottom: $value;
  }
}

// margin-left
@each $key,$value in $spacers{
  .ml-#{$key}{
    margin-left: $value;
  }
}

// margin-right
@each $key,$value in $spacers{
  .mr-#{$key}{
    margin-right: $value;
  }
}

.ml-auto{
  margin-left: auto;
}
.mr-auto{
  margin-right: auto;
}
.mt-auto{
  margin-top: auto;
}
.mb-auto{
  margin-bottom: auto;
}