/*------------------------------------*\
    $variable
\*------------------------------------*/
$spacer: 1rem;
$spacers: (
    0: 0,
    1: $spacer / 4,
    2: $spacer / 2,
    3: $spacer,
    4: $spacer * 1.5,
    5: $spacer * 2,
    6: $spacer * 4,
);

$font-size-base: 1rem;
$font-size:(
    0:$font-size-base * 0,
    1:$font-size-base * 2.5,
    2:$font-size-base * 2,
    3:$font-size-base * 1.75,
    4:$font-size-base * 1.5,
    5:$font-size-base * 1.25,
    6:$font-size-base * 1,
);

$color-font-base: #650300;
$color-font-caption: #000000;
$color-font-link: #ffffff;
$color-font-white: #ffffff;
$color-font-date: #707070;
$color-font-pagination: #555555;
$color-font-hover: #ffffff;

$color-border-base: #000000;
$color-border-white: #ffffff;
$color-border-recommend: #DEE2E6;
$color-border-title: #9F1213;
$color-border-pagination: #555555;


$color-bgc-primary: #AA0601;
$color-bgc-secondary: #FBF2F2;
$color-bgc-feature: #FBF2F2;
$color-bgc-figcaption: #ffffff;
$color-bgc-contact: #FBF2F2;
$color-bgc-input: #FBF2F2;
$color-bgc-link: #DCDCDC;
$color-bgc-Select-primary :#5F3E2D;
$color-bgc-Select-secondary :#B75929;

$color-hover-Select-pagination: #000000;

$font-family-base: "Noto Sans TC", sans-serif;
$font-family-English: "Roboto", sans-serif;


$btn-font-family: $font-family-base;
$btn-font-weight: 400;
$btn-line-height: 1.5;
$body-color: transparent;
$link-decoration: underline !default;
$btn-white-space: null !default;
$enable-button-pointers: true !default;
$btn-border-width: 0px //!default;